'use strict'; // MODULES //

var isLittleEndian = require('@stdlib/assert/is-little-endian'); // MAIN //


var indices;
var HIGH;
var LOW;

if (isLittleEndian === true) {
  HIGH = 1; // second index

  LOW = 0; // first index
} else {
  HIGH = 0; // first index

  LOW = 1; // second index
}

indices = {
  'HIGH': HIGH,
  'LOW': LOW
}; // EXPORTS //

module.exports = indices;