'use strict'; // MODULES //

var ctors = require('./ctors.js'); // VARIABLES //


var bool; // FUNCTIONS //

/**
* Returns a boolean indicating if an environment is little endian.
*
* @private
* @returns {boolean} boolean indicating if an environment is little endian
*
* @example
* var bool = isLittleEndian();
* // returns <boolean>
*/

function isLittleEndian() {
  var uint16view;
  var uint8view;
  uint16view = new ctors['uint16'](1);
  /*
  * Set the uint16 view to a value having distinguishable lower and higher order words.
  *
  * 4660 => 0x1234 => 0x12 0x34 => '00010010 00110100' => (0x12,0x34) == (18,52)
  */

  uint16view[0] = 0x1234; // Create a uint8 view on top of the uint16 buffer:

  uint8view = new ctors['uint8'](uint16view.buffer); // If little endian, the least significant byte will be first...

  return uint8view[0] === 0x34;
} // MAIN //


bool = isLittleEndian(); // EXPORTS //

module.exports = bool;