'use strict'; // MODULES //

var isOdd = require('./../../../../base/assert/is-odd');

var copysign = require('./../../../../base/special/copysign');

var NINF = require('@stdlib/constants/float64/ninf');

var PINF = require('@stdlib/constants/float64/pinf'); // MAIN //

/**
* Evaluates the exponential function when \\(|x| = 0\\).
*
* @private
* @param {number} x - base
* @param {number} y - exponent
* @returns {number} function value
*
* @example
* var v = pow( 0.0, 2 );
* // returns 0.0
*
* @example
* var v = pow( -0.0, -9 );
* // returns -Infinity
*
* @example
* var v = pow( 0.0, -9 );
* // returns Infinity
*
* @example
* var v = pow( -0.0, 9 );
* // returns 0.0
*
* @example
* var v = pow( 0.0, -Infinity  );
* // returns Infinity
*
* @example
* var v = pow( 0.0, Infinity );
* // returns 0.0
*/


function pow(x, y) {
  if (y === NINF) {
    return PINF;
  }

  if (y === PINF) {
    return 0.0;
  }

  if (y > 0.0) {
    if (isOdd(y)) {
      return x; // handles +-0
    }

    return 0.0;
  } // y < 0.0


  if (isOdd(y)) {
    return copysign(PINF, x); // handles +-0
  }

  return PINF;
} // EXPORTS //


module.exports = pow;